import React from 'react'

class Presentation extends React.Component {
    render() {
        return (
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                <div style={{ textAlign: 'center', }}>
                    <img src="/images/me.jpg" alt="me" style={{ width: 250, height: 250, borderRadius: 150 }}/>
                    <h4>Hi!, I'm Bruno Farias</h4>
                </div>
                <div style={{ maxWidth: 750, padding: 20, textAlign: 'center' }} >
                    Lover and programming student since 2015, I've been work with PHP, ReactJS, React Native and NodeJS, as well as being familiar with other programming languages ​​and frameworks, such as Python, Flask, Symfony and Laravel. I always strive to be updated with the new challenges of the development world. 
                </div>
                <div style={{ marginTop: 75 }}>
                    <span uk-icon="icon: chevron-down; ratio: 1"></span>
                </div>
            </div>
        )
    }
}

export default Presentation
