import React from 'react'

class Contact extends React.Component {
    render() {
        return (
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', color: '#FFF', paddingTop: 50 }}>
                <div style={{ textAlign: 'center', color: '#FFF', minWidth: 320}}>
                    <h4 style={{ color: '#FFF' }}>Contact me</h4>
                    <form>
                        <div className="uk-margin">
                            <input className="uk-input" type="text" placeholder="Name"/>
                        </div>
                        <div className="uk-margin">
                            <input className="uk-input " type="text" placeholder="Email"/>
                        </div>
                        <div class="uk-margin">
                            <textarea class="uk-textarea" rows="5" placeholder="Message"></textarea>
                        </div>
                        <button style={{ color: '#FFF' }} class="uk-button uk-button-default uk-width-1-1 uk-margin-small-bottom">SEND</button>
                    </form>
                </div>
                <div>

                </div>
                <div style={{ padding: 40, maxWidth: 500 }}>
                    <p>
                        If you like my work, please contact me sending  a message by this form or contact me in my email <a href="mailto:bruno27ffarias@gmail.com">contato@brunofarias.dev.br</a>
                    </p>
                    <p>
                        Osasco - SP, Brazil
                    </p>
                    <ul className="uk-navbar-nav">
                        <li><a href="https://www.instagram.com/mbrunofarias/" target="_blank"><span uk-icon="icon: instagram; ratio: 1"></span></a> </li>
                        <li><a href="https://www.linkedin.com/in/brunoffarias27/" target="_blank"><span uk-icon="icon: linkedin; ratio: 1"></span></a> </li>
                        <li><a href="https://github.com/brunoofarias" target="_blank"><span uk-icon="icon: github; ratio: 1"></span></a> </li>
                    </ul>
                    <p>
                        Bruno Farias 2020 - All rights reserved
                    </p>
                </div>
            </div>
        )
    }
}

export default Contact
