import React, { Component } from 'react'
import 'uikit/dist/css/uikit.css'
import Navbar from './components/navbar'
import Presentation from './components/presentation'
import Skills from './components/skills'
import Projects from './components/projects'
import Contact from './components/contact'
import './App.css'

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Navbar/>
        </header>
        <session style={{ height: 'calc(100vh - 10px)', display: 'flex', alignItems: 'center', justifyContent: 'center' }} >
          <Presentation />
        </session>
        <session id="projects" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} >
          <Projects />
        </session>
        <session id="articles" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} >
          <Skills />
        </session>
        <session id="contact" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', background: '#222222' }} >
          <Contact />
        </session>
      </div>
    )
  }
}

export default App
